import { injectGlobal } from 'styled-components';

// eslint-disable-next-line
injectGlobal`
    @import url('https://fonts.googleapis.com/css?family=Montserrat');

    body{
        margin: 0;
        padding: 0;
        font-family: 'Montserrat', sans-serif;
        font-size: 24px;
    }
`;


